
import XHeader from 'vux/src/components/x-header/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

import axios from 'axios'
export default {
components: {
XHeader,
ViewBox,
Loading,
Tabbar,
TabbarItem
},
name: 'app',
data () {
return {
title:'首页',
show:false,
showTab:true

}
},
computed:{
direction(){
return this.$store.state.direction
}
},
methods:{
getTitle(){
let routerUrl = this.$route.path;

if(routerUrl == '/home'){
return '首页'
}else if(routerUrl == '/order'){
return '订单'
}else {
return '个人中心'
}
}
},
mounted(){




}
}



