import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '*',
      redirect: '/home'
    },
    {
      path: '/home',
      component: function(reslove){
        return require(['../components/Home'],reslove)
      }
    },
    {
      path: '/order',
      component: function(reslove){
        return require(['../components/Order'],reslove)
      }
    },
    {
      path: '/PersonCenter',
      component: function(reslove){
        return require(['../components/PersonCenter'],reslove)
      }
    }
  ]
})



